.App-header {
  background-color: rgb(245, 245, 245);
  height: 100vh;
  display: flex;
  font-size: calc(10px + 2vmin);
  color: gray;
}

#top-box {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 15%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.patient-info {
  font-size: 12px;
}

.shadow-title {
  color: black;
}

#text-container {
  position: fixed;
  top: 15%;
  left: 0;
  right: 0;
  height: 65%;
  overflow: auto;
}

.controls-container {
  display: flex;
  flex-direction: row;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20%;
  cursor: pointer;
}

.finish-button {
  height: 100%;
  width: 20%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  cursor: pointer;
}

.important {
  color: green;
}

.concerning {
  color: blue;
}

.alarming {
  color: red;
}

.standard {
  color: black;
}

.start-finish-box {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}